export default {
	data() {
		return {
			swiperList:[]
		}
	},
	onLoad() {
		this.getSwiper();
	},
	
	methods: {
		//获取轮播图数据
		async getSwiper(){
			const res = await this.$myRequest({
				url:'/api/getlunbo'
			})
			console.log(res);
			this.swiperList=res.data.message
		}
	}
}
